import React from 'react';
import { NavLink } from 'react-router-dom';
import './sideBarStyle.css';
export const SideBar = (props) => {
    return (
        <>
            <ul id='sideBar'>
                <li><NavLink activeClassName="active" className='link' to='/RoomPackage'><i className="fas fa-truck"/>MY MOVES</NavLink></li>
                <li><NavLink className='link' to='/RoomPackage/MyProfile'><i className="fas fa-user"/>MY PROFILE</NavLink></li>
                <li><NavLink className='link' to='/RoomPackage/GetQuote'><i className="fas fa-calculator"/>GET QUOTE</NavLink></li>
                <li><NavLink className='link' to='/RoomPackage/Logout'><i className="fas fa-sign-out-alt"/>LOGOUT</NavLink></li>
            </ul>
        </>
    )
}